import React, { createContext, useContext, useEffect, useState } from "react";
import { Media } from "../models/Media";

interface FavoriteContextProps {
  favorites: Media[];
  addFavorite: (item: Media) => void;
  removeFavorite: (id: number) => void;
}

const FavoriteContext = createContext<FavoriteContextProps | undefined>(
  undefined
);

export const useFavorite = () => {
  const context = useContext(FavoriteContext);
  if (!context) {
    throw new Error("FavoriteProvider   ?");
  }
  return context;
};

export const FavoriteProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  //     +    
  const [favorites, setFavorites] = useState<Media[]>(() => {
    const storedFavorites = localStorage.getItem("favorites");
    if (storedFavorites) {
      try {
        return JSON.parse(storedFavorites);
      } catch (e) {
        console.error("     .", e);
        return [];
      }
    }
    return [];
  });

  //   
  useEffect(() => {
    localStorage.setItem("favorites", JSON.stringify(favorites));
  }, [favorites]);

  //  
  const addFavorite = (media: Media) => {
    setFavorites((prev) => {
      if (prev.some((fav) => fav.id === media.id)) {
        return prev;
      }
      const updatedFavorites = [...prev, media];

      return updatedFavorites;
    });
  };

  //  
  const removeFavorite = (id: number) => {
    setFavorites((prev) => prev.filter((item) => item.id !== id));
  };

  return (
    <FavoriteContext.Provider
      value={{ favorites, addFavorite, removeFavorite }}
    >
      {children}
    </FavoriteContext.Provider>
  );
};
